<script>
import eosLogo from '@/assets/eos.png';
import Eos from 'eosjs';
import eventHub from '@/utils/event';
import network from '@/utils/network';
import fetch from '@/utils/api';
import api from '@/utils/eos';
import config from '@/utils/config';
import createHash from 'create-hash';
import vueSlider from '@/components/slider.vue'

export default {
  mounted () {
    this.TimeDown()
    var that =this
    this.$i18n.locale = this.$lan()
    this.language = this.$lan()
    eventHub.$on('SHOW_ABOUT', () => this.showAbout = true);
    eventHub.$on('SHOW_SOCIAL', () => this.showSocial = true);
    eventHub.$on('SHOW_REFERRALS', () => this.showReferrals = true);
    eventHub.$on('SHOW_CUSTOMSEED', () => this.showCustomSeed = true);
    this.getEOS();
    // this.getPool();
    this.hitCharts(this.hbBtn.toLowerCase(),3)
    this.$nextTick(function () {
      if (typeof (scatter) !== 'undefined') {
        if (scatter.identity) {
          const account = scatter.identity.accounts.find(account => account.blockchain === 'eos');
          that.$store.commit('UPDATE_ACCOUNT', account);
        }
        if (!that.$store.state.account || !that.$store.state.account.name) {
          that.login()
        }
      } else {
        document.addEventListener('scatterLoaded', function () {
          if (scatter.identity) {
            const account = scatter.identity.accounts.find(account => account.blockchain === 'eos');
            that.$store.commit('UPDATE_ACCOUNT', account);
          }
          if (!that.$store.state.account || !that.$store.state.account.name) {
            that.login()
          }
        })
      }
    })
  },

  data () {
    return {
      phBtn:1,
      autoBtn:false,
      hitOpen:false,
      winOpen:false,
      hbBtn:'EBTC',
      btnType:0,
      hitImg: '',
      height:'',
      reward:{
        0:'¥2000',
        1:'比特护盾',
        2:'比特护盾',
        3:'5 EOS',
        4:'2 EOS',
        5:'1 EOS'
      },
      ownWin: {
        name:'-',
        time:'-',
        ph:'-',
        wins:'-',
        reward:'-'
      },
      token:{
        EBTC:0,
        EETH:0,
        EUSD:0,
        EOS:0
      },
      hitChartsData:'',//连击榜数据
      sliderModel: {
        value: 50,
        width: '100%',
        height: 12,
        direction: 'horizontal',
        dotSize: 16,
        eventType: 'auto',
        min: 2,
        max: 96,
        minRange: 2,
        maxRange: 96,
        interval: 1,
        startAnimation: false,
        tooltipMerge: true,
        debug: process && process.env && process.env.NODE_ENV !== 'production',
        disabled: false,
        show: true,
        realTime: false,
        tooltip: 'always',
        clickable: true,
        tooltipDir: 'top',
        piecewise: false,
        lazy: false,
        useKeyboard: false,
        reverse: false,
        speed: 0.5,
        focusStyle: null,
        bgStyle: {
          "background-color": "#f10260",
        },
        sliderStyle: {
          "background": "#f6f6f6",
          "background-image":"url('/image/lock.png')",
          "border": "1px solid #c5c5c5",
          "font-weight": "normal",
          "border-radius": "0"
        },
        tooltipStyle: null,
        processStyle: {
          "background-color": "#02f292",
          "box-shadow": "0 0 20px #02f292",
          "left": "0",
          "border-radius": "5px"
        },
        piecewiseStyle: null,
        disabledStyle: null,
        maxWidth: 96,
        showUpAnimation: false,
        showDownAnimation: false
      },
      eosLogo,
      eos:0.000001,
      eosWin:0.000002,
      currentEOS: 0,
      poolBalance: 0,
      timer: 0,
      animationTxt: 0,
      actionTxt: this.$t('start'),
      showAbout: false,
      showSocial: false,
      showReferrals: false,
      showCustomSeed: false,
      animating: false,
      showUpAnimation: false,
      showDownAnimation: false,
      language: 'en',
      customSeed: null,
      btnDisable: false,
      loadTimer: null,
      rollValue: 0,
      ljopen:false,
      day_title:this.$t('day_title'),
      all_title:this.$t('all_title'),
      overTime:'00:00:00',
      eosIcon:require('../assets/icon_ebtc_w.png'),
      lastWins:0
    };
  },
  methods: {
    PrefixInteger(num, n) {
      return (Array(n).join(0) + num).slice(-n);
    },
    zcClick(){
      this.hitOpen= false
      this.winOpen= false
    },
    copyText () {
      this.$copyText(this.referralsText)
      this.$refs.ipt.select();
    },
    btnClick(num) {
      this.btnType = num
      if(num === 0){
        this.sliderModel.bgStyle = {"background-color": "#f10260",}
        this.sliderModel.value = 50
        this.sliderModel.processStyle = {
          "background-color": "#02f292",
          "box-shadow": "0 0 20px #02f292",
          "left": "0",
          "border-radius": "5px"
        }
      }else if(num === 1){
        this.sliderModel.bgStyle = {"background-color": "#02f292",}
        this.sliderModel.value = 51
        this.sliderModel.processStyle = {
          "background-color": "#f10260",
          "box-shadow": "0 0 20px #f10260",
          "left": "0",
          "border-radius": "5px"
        }
      }
    },
    getEOS (isAnimation) {
      if (!this.account.name) {
        this.token.EOS = 0;
        this.currentEOS = 0;
        return;
      }
      if (isAnimation) {
        this.showDownAnimation = true;
        this.animationTxt = this.eos
        setTimeout(() => {
          this.showDownAnimation = false;
        }, 1100);
      }
      this.getToken()
      return api.getAccount(this.account.name).then(({ core_liquid_balance }) => {
        this.token.EOS =Number(core_liquid_balance.replace(/\sEOS/, '')).toFixed(4);
      });

    },
    getToken(tokenName){
      if(!this.account.name){
        return
      }
      api.getTableRows({
        json: true,
        code: 'bitpietokens',
        table: 'accounts',
        scope: this.account.name
      }).then((results)=> {
        console.log(results.rows)
        if(results.rows&&results.rows.length!=0){
          for(var i=0;i<results.rows.length;i++){
            var str = results.rows[i].balance.split(" ");
            if(str[1]==='EBTC'){
              this.token.EBTC = Number(str[0]).toFixed(6)
            }else if(str[1]==='EETH'){
              this.token.EETH = Number(str[0]).toFixed(4)
            }else if(str[1]==='EUSD'){
              this.token.EUSD = Number(str[0]).toFixed(2)
            }
          }
          if(this.hbBtn === 'EBTC'){
            this.currentEOS = this.token.EBTC
          }else if(this.hbBtn === 'EETH'){
            this.currentEOS = this.token.EETH
          }else if(this.hbBtn === 'EUSD'){
            this.currentEOS = this.token.EUSD
          }
        }

  })
    },
    getPool () {
      Promise.all([
        api.getTableRows({
          json: true,
          code: 'eosio.token',
          table: 'accounts',
          scope: config.diceAccount
        }),
        api.getTableRows({
          json: true,
          code: config.diceAccount,
          table: 'fundpool',
          scope: config.diceAccount
        })
      ]).then(([accountBalance, poolBalance]) => {
        if (poolBalance.rows[0] && poolBalance.rows[0].locked && accountBalance.rows[0] && accountBalance.rows[0].balance) {
          this.poolBalance = accountBalance.rows[0].balance.slice(0, -4)
            - poolBalance.rows[0].locked.slice(0, -4);
        } else {
          if (accountBalance.rows[0] && accountBalance.rows[0].balance) {
            this.poolBalance = accountBalance.rows[0].balance.slice(0, -4)
          }
        }
      });
    },
    floor (value, decimals) {
      return Number(Math.floor(value + 'e' + decimals) + 'e-' + decimals);
    },
    maxBetAmount () {
      return this.floor(this.poolBalance / 20 / (98.5 / this.winChance) * 0.9, 4);
    },
    setEOS (rate) {
      const { poolBalance, currentEOS } = this;
      let eos = rate ? this.eos * rate : this.currentEOS;
      switch (true) {
        case (eos < 0.1):
          eos = 0.1;
          break;
        case (eos > currentEOS):
          eos = currentEOS;
          break;
        case (eos > this.maxBetAmount()):
          eos = this.maxBetAmount();
          break;
      }
      this.eos = Number(eos).toFixed(4);
    },
    getClientSeed () {
      if (this.customSeed) {
        return createHash('sha1').update(this.account.name + this.customSeed).digest('hex');
      } else {
        let randomNumber = Math.floor(Math.random() * Math.floor(Number.MAX_SAFE_INTEGER));
        return createHash('sha1').update(this.account.name + Date.now() + randomNumber).digest('hex');
      }
    },
    updateSeed () {
      this.showCustomSeed = false
    },
    doAction () {
      eventHub.$emit('HIDDEN_MENU');
      this.btnDisable = true
      const body = new FormData();
      const eos = scatter.eos(network, Eos, {})
      this.$message({
        showClose: true,
        message: this.$t('confirm_transfer'),
        type: 'info',
        duration: 1500
      });
      let referrer = this.getRequest('ref');
      body.append('roll_under', this.sliderModel.value);
      body.append('referrer', referrer);

      const now = new Date();
      const expiration = Math.floor(now.getTime() / 1000);
      var clientSeed =this.getClientSeed()
      var money;
      var account;
      if(this.hbBtn !='EOS'){
        money = Number(this.eos).toFixed(8)
        account = 'bitpietokens'
      }else{
        money = Number(this.eos).toFixed(4)
        account = 'eosio.token'
      }
      console.log(money + ' '+ this.hbBtn)
      eos.transaction({
        actions: [{
          account: account,//合约名，
          name: "transfer",//
          authorization: [{
            actor: this.account.name,
            permission: this.account.authority
          }],
          data: {
            from: this.account.name,
            to: config.diceAccount,
            quantity:money + ' '+ this.hbBtn,
            memo: this.btnType+`|`+clientSeed
          }
        }]
      }).then(() => {
        this.getEOS(true);
        let index = 0;
        this.fetchResult(this.account.name, clientSeed);
        this.animating = true;
        this.$message({
          showClose: true,
          message: this.$t('bet_result'),
          type: 'info',
          duration: 1500
        });
      }).catch(e => {
        this.btnDisable = false
        this.autoBtn = false
        this.$notify.error(e.message || JSON.parse(e).error.details[0].message);
      });
    },
    getRequest (name) {
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
      var r = window.location.search.substr(1).match(reg);
      var context = "";
      if (r != null)
        context = r[2];
      reg = null;
      r = null;
      return context == null || context == "" || context == "undefined" ? config.diceAccount : context;
    },
    fetchResult (player, clientSeed) {//结果处理
      console.log(clientSeed)
      var player = player
      var clientSeed = clientSeed
      api.getTableRows({
        json: true,
        code: 'bitpiedaisiu',
        table: 'results',
        scope: 'bitpiedaisiu',
        limit: 200
      }).then((results) => {
        if(results&&results.rows){
          var data = false
          var rows = results.rows.reverse()
          console.log(rows)
          for(var i in rows){
            if(rows[i].user_seed === clientSeed){
              data = rows[i]
              break
            }
          }
          if(data){
            console.log(data)
            this.animating = false;
            this.btnDisable = false;
            if (data.big_small==0){
              if(data.result<=50){
                this.getWins()
              }else{
                this.$message({
                  showClose: true,
                  message: this.$t('bet_fail_p',{result:data.big_small==0?'大':'小',amount:data.result}),
                  type: 'info',
                  duration: 1500
                });
              }
            }else if(data.big_small ==1){
              if(data.result>=51){
                this.getWins()
              }else{
                this.$message({
                  showClose: true,
                  message: this.$t('bet_fail_p',{result:data.big_small==0?'大':'小',amount:data.result}),
                  type: 'info',
                  duration: 1500
                });
              }
            }
            if(this.autoBtn){
              this.doAction()
            }
          }else{
            this.fetchResult(player,clientSeed)
          }
          console.log(results)
        }
      }).catch(e=>{
        console.log(e)
        this.autoBtn = false
      })
    },
    login () {
      eventHub.$emit('HIDDEN_MENU');
      if (scatter) {
        scatter.getIdentity({
          accounts: [network]
        }).then(() => {
          scatter.authenticate()
          const account = scatter.identity.accounts.find(account => account.blockchain === 'eos');
          if (!account) return;
          this.$store.commit('UPDATE_ACCOUNT', account);
          this.$message({
            showClose: true,
            message: this.$t('login_success'),
            type: 'success',
            duration: 1500
          });
        }).catch(e => {
          this.$message({
            showClose: true,
            message: e.message,
            type: 'warning',
            duration: 1500
          });
        });
      }
    },
    checkBetamount () {
    },
    navigate (brand) {
      switch (brand) {
        case 'twitter':
          window.open('http://twitter.com/EOSDice');
          break;
        case 'telegram':
          window.open('https://t.me/joinchat/F2x9wxBaewZ_t5owSEHc2Q');
          break;
        case 'github':
          // window.open('//github.com/dappub');
          break;
        case 'discord':
          // window.open('//discordapp.com/channels/482077322070196225/487187255065313292');
          break;
      }
    },
    openLj(num){
      if(num===0){
        this.ljopen=true
      }else if(num===1){
        this.ljopen=false
      }

    },
    ph_Btn(type){
      this.phBtn = type
      if(type ===1){
        this.hitCharts(this.hbBtn.toLowerCase())
      }else{
        this.hitCharts(this.hbBtn.toLowerCase(),3)
      }

    },
    TimeDown(endDate) {
      //结束时间
      var that = this
      var endDate = new Date(new Date(new Date().toLocaleDateString()).getTime()+24*60*60*1000-1);
      //当前时间
      var nowDate = new Date();
      //相差的总秒数
      var totalSeconds = parseInt((endDate - nowDate) / 1000);
      //天数
      var days = Math.floor(totalSeconds / (60 * 60 * 24));
      //取模（余数）
      var modulo = totalSeconds % (60 * 60 * 24);
      //小时数
      var hours =this.PrefixInteger(Math.floor(modulo / (60 * 60)),2);
      modulo = modulo % (60 * 60);
      //分钟
      var minutes = this.PrefixInteger(Math.floor(modulo / 60),2);
      //秒
      var seconds = this.PrefixInteger(modulo % 60,2);
      //输出到页面

      this.overTime =  hours + ":" + minutes + ":" + seconds ;
      //延迟一秒执行自己

      setTimeout(function () {
        that.$options.methods.TimeDown.bind(that)();
      }, 1000)

    },
    hit(num,height){
      var html = ''

      this.height=height
      for(var i=0;i<num.length;i++){
        var src = require('../assets/'+num[i]+'@2x.png')
        html += '<img class="hit-img" src='+src+' style="height:'+height+'px">'
      }
      html += '<img class="hit-img" src='+require("../assets/hits.png")+' style="height:'+height+'px">'
      this.hitImg = html
      this.hitOpen = true
    },
    hbClick(type){
      this.hbBtn = type
      this.getToken()

      if(type === 'EBTC'){
        this.eosIcon = require('../assets/icon_ebtc_w.png')
        this.eos = 0.000001
        this.eosWin= 0.000002
        this.currentEOS = this.token.EBTC
      }else if(type === 'EETH'){
        this.eos = 0.0001
        this.eosWin= 0.0002
        this.eosIcon = require('../assets/icon_eeth_w.png')
        this.currentEOS = this.token.EETH
      }else if(type === 'EUSD'){
        this.eos=0.01
        this.eosWin= 0.02
        this.eosIcon = require('../assets/icon_eusdt_w.png')
        this.currentEOS = this.token.EUSD
      }else if(type === 'EOS'){
        this.eos = 0.001
        this.eosWin= 0.002
        this.currentEOS = this.token.EOS
        this.eosIcon = require('../assets/logo_eos_w@2x.png')
      }
      this.hitCharts(this.hbBtn.toLowerCase())
    },
    hitCharts(coinname,day){
      var coinname = coinname.toLowerCase();
      var day = day || ''
      var data = {
        name:'-',
        time:'-',
        ph:'-',
        wins:'-',
        reward:'-'
      }
      api.getTableRows({
        "code": "bitpiedaisiu",
        "table": "scores",
        "json": "true",
        "key_type": "i64",
        "index_position": "2",
        "limit": 20,
        scope: coinname + day
      }).then((results) => {
        console.log(results)
        if(results.rows){
          this.hitChartsData = results.rows
          if(this.account.name){
            data.name = this.account.name
            api.getTableRows({
              "scope": coinname + day,
              "code": "bitpiedaisiu",
              "table": "scores",
              "json": "true",
              "key_type": "i64",
              "index_position": "1",
              "lower_bound":this.account.name,
              "limit": 1
            }).then((results) => {
              if(results.rows&&results.rows[0]){
                data.time=results.rows[0].lost_at;
                data.wins = results.rows[0].wins;
                for(var i=0;i<this.hitChartsData.length;i++){
                  if(this.hitChartsData[i].player === this.account.name){
                    data.ph = i+1
                  }
                }
              }
              this.ownWin = data
            })
          }
        }
      })
    },
    getWins(coinname){
      var coinname = coinname;
      api.getTableRows({
        "scope": "eos",
        "code": "bitpiedaisiu",
        "table": "scores",
        "json": "true",
        "key_type": "i64",
        "index_position": "1",
        "lower_bound":this.account.name,
        "limit": 1
      }).then((results) => {
        console.log(results)
        if(results.rows){
          var wins = results.rows[0].last_won+results.rows[0].last_count
          this.lastWins = wins
          var that = this
          if(wins<3){
            this.winOpen= true
          }else if(wins>3){
            this.hit(wins,'60')
          }
          if(this.autoBtn){
            setTimeout(function(){
              that.zcClick()
            },1000)
          }
        }
      })
    },
    changeDate(datetimes){
      var date = new Date(datetimes/1000);
      if(date&&date.getHours()){
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        var D = date.getDate() + ' ';
        var h = this.PrefixInteger(date.getHours(),2) + ':';
        var m = this.PrefixInteger(date.getMinutes(),2) + ':';
        var s = this.PrefixInteger(date.getSeconds(),2);
//      年 月 日 时 分 秒
        return h+m+s;
      }else{
        return '--:--:--'
      }

    },
    nameCut(str){
      var str = str
      if(str.length<=6){
        return str
      }else{
        return str.substring(2,0) + '**'+ str.substring(str.length,str.length-2)
      }
    }

  },

  watch: {
    account () {
      this.getEOS();
      this.getToken();
    },
    animating () {
      const { animating } = this;
      if (!animating) {
        clearInterval(this.timer);
        this.actionTxt = this.$t('start');
        return;
      }
      this.timer = setInterval(() => {
        this.actionTxt = (Math.random() * 100).toFixed(0);
      }, 100);
    }
  },
  components: {
    vueSlider
  },
  computed: {
    referralsText () {
      if (this.$store.state.account.name) {
        return 'https://dice.betsand.com/?ref=' + this.$store.state.account.name
      }
      return 'https://dice.betsand.com/?ref='
    },
    winChance () {
      return this.sliderModel.value - 1;
    },
    payOut () {
      return 98.5 / this.winChance;
    },
    payWin () {
      return (this.eos * this.payOut).toFixed(4);
    },
    account () {
      return this.$store.state.account;
    },
    betAmount () {
      return this.$t('bet_amount')
    },
    payoutWin () {
      return this.$t('pay_out_win')
    },
    rollUnderWin () {
      return this.$t('roll_under_win')
    },
    rollUnderWinDa () {
      return this.$t('roll_under_win_da')
    },
    payoutLan () {
