<template>
  <section class="orders-container">
    <header class="orders-tab">
      <ul>
        <li :class="{'on':orderType===1}" @click="orderClick(1)">{{allBet}}</li>
        <li :class="{'on':orderType===2}" @click="orderClick(2)">{{myBet}}</li>
      </ul>
    </header>
      <table class="orders-table">
        <tr>
          <th class="hidden-md-and-down">{{time}}</th>
          <th>{{bettor}}</th>
          <th class="hidden-xs-only">{{roll_under}}</th>
          <th class="hidden-500">{{bet}}</th>
          <th class="hidden-xs-only">{{roll}}</th>
          <th class="hidden-360">{{payout}}</th>
        </tr>
      </table>
    <div class="tbody-box">
      <table class="orders-table">
        <tr :key="index" v-for="(order, index) in orders" :class="order.prize !== '0.0000 EOS' ? 'success':'error'">
          <td class="hidden-md-and-down">{{dateFormat(order.created_at)}}</td>
          <td>{{order.player}}</td>
          <td class="hidden-xs-only">{{order.result}}</td>
          <td class="hidden-500">{{order.amount}}</td>
          <td class="hidden-xs-only">{{order.big_small}}</td>
          <td class="payout hidden-360">
            {{priceCut(order.prize)}}
          </td>
        </tr>
      </table>
    </div>

  </section>
</template>

<script>
import api from '@/utils/eosOrder';
import config from '@/utils/config';

export default {
  mounted () {
    this.fetchOrders()
    setInterval(this.fetchOrders, 10000);
  },

  data () {
    return {
      orders: [],
      orderType:1,
    };
  },

  methods: {
    fetchOrders () {
      var that = this
      api.getTableRows({
        json: true,
        code: 'bitpiedaisiu',
        table: 'results',
        scope: 'bitpiedaisiu',
        limit: 200
      }).then((results) => {
        results.rows.reverse()
        if(results&&results.rows){
          if(that.orderType===1){
            that.orders = results.rows
          }else if(that.orderType===2){
            var data = []
            for(var i in results.rows){
              if(this.account.name === results.rows[i].player){
                data.push(results.rows[i])
              }
            }
            console.log(data)
            that.orders = data
          }

        }

      })
    },

    dateFormat (raw) {
      return new Date(raw + 'Z').toLocaleTimeString();
    },
    orderClick(num){
      if(num === 1){
        this.orderType = num
        this.fetchOrders()
      }else if(num === 2 && this.account){
        this.orderType = num
        this.fetchOrders()
      }else{
        this.orderType = 1
      }

    },
    priceCut(str){
      var str = str.split(" ");
      if(parseFloat(str[0])===0){
        return ''
      }
      return parseFloat(str[0]) +' ' + str[1]
    }
  },

  computed: {
    allBet () {
      return this.$t('all_bet');
    },
    myBet () {
      return this.$t('my_bet');
    },
    time () {
      return this.$t('time');
    },
    bettor () {
      return this.$t('bettor');
    },
    roll_under () {
      return this.$t('roll_under');
    },
    bet () {
      return this.$t('bet');
    },
    roll () {
      return this.$t('roll');
    },
    payout () {
      return this.$t('list_payout');
    },
    account() {
      return this.$store.state.account
    }
  }
};
</script>

